import React, { Component, PropTypes } from 'react'

class Option extends Component {

  constructor(props) {
    super(props)
    this.state = Object.assign({}, props, {
      text: props.children
    })
  }

  handleOptionClicked() {
    this.context.handleOptionClicked(this.state)
  }

  render() {
    return <span onClick={this.handleOptionClicked.bind(this)}>{this.state.text}</span>
  }
}

Option.contextTypes = {
  handleOptionClicked: React.PropTypes.func
}

export default Option


