import React, { Component, PropTypes } from 'react'
import Option from './Option'

/**
 * 
 */

class Select extends Component {

  constructor(props) {
    super(props)
    this.state = Object.assign({}, props, {
      active: false
    })
  }

  getChildContext() {
    return {
      handleOptionClicked: this.handleOptionClicked.bind(this)
    }
  }

  handleOptionClicked(data) {
    let newState = Object.assign({}, data, {
      active: false
    })
    this.setState(newState)
  }

  handleSelectClicked() {
    this.props.onClick()
    this.setState({
      active: !this.state.active
    })
  }

  render() {

    let {
      onClick,
      ...props
    } = this.props

    let {
      text,
      active
    } = this.state

    let className = this.props.className

    return <div {...props}>
        <span className={className + '-selected'} onClick={this.handleSelectClicked.bind(this)}>{text}</span>
        <span className={className + '-popup' + (active ? '' : ' hide')}>{this.props.children}</span>
    </div>
  }
}

Select.Option = Option

Select.defaultProps = {
  className: 'zent-select',
  text: '',
  placeholder: '',
  onClick: function() {
    console.log('click Zent Select!');
  }
}

Select.childContextTypes = {
  handleOptionClicked: React.PropTypes.func
}

Select.context

export default Select


